          <b-input-group>
            <b-form-select v-model="filter" :options="sortBy">
              <option slot="first" :value="null">Todos</option>
            </b-form-select>
          </b-input-group>
          </b-form-group>
      </div>
        <b-table 
            show-empty 
            :fields="fields" 
            :items="items" 
            :current-page="currentPage" 
            :per-page="perPage"
            striped hover
            empty-text="No hay datos para mostrar"
            :filter="filter"
            @row-clicked="info"
        >
            <template slot="seleccion" slot-scope="row">
            <b-form-checkbox v-model="seleccion[row.item.folio]" :key="row.item.folio" :value="row.item"/>
            </template>
        </b-table>
        <b-pagination :total-rows="totalRows" :per-page="perPage" v-model="currentPage" class="my-0" />
    </b-tab>
    <b-tab title="Alternativas de InversiÃ³n" :disabled="disabled[1]">
        <h1> por definir contenido</h1>
    </b-tab>
    <b-tab title="Mis Devoluciones" :disabled="disabled[2]">
        <b-table 
            show-empty 
            :fields="fields" 
            :items="items" 
            :current-page="currentPage" 
            :per-page="perPage" 
            empty-text="No hay datos para mostrar"
        >
            <template slot="seleccion" slot-scope="row">
            <b-form-checkbox v-model="seleccion[row.item.folio]" :key="row.item.folio" :value="row.item"/>
            </template>
        </b-table>
        <b-pagination :total-rows="totalRows" :per-page="perPage" v-model="currentPage" class="my-0" />
    </b-tab>
  </b-tabs>
  </b-card>


  <!--MODAL-->
  <b-modal size="lg" id="modalInfo" @hide="resetModal" :title="modalInfo.title" ok-only>
<b-card no-body>
<b-tabs card v-model="modalIndex">
    <b-tab title="Detalle" :disabled="disabled[0]">
        <b-container>
            <b-row>
                <b-col>
                    <h2>Proyecto</h2>
                    </b-col>
                    </b-row>
            <b-row>
                <b-col>
        <b-table 
            show-empty 
            :fields="fieldsInv" 
            :items="itemsInv" 
            striped hover stacked
            empty-text="No hay datos para mostrar"
        >
            <template slot="seleccion" slot-scope="row">
            <b-form-checkbox v-model="seleccion[row.item.folio]" :key="row.item.folio" :value="row.item"/>
            </template>
            </b-table>
            </b-col>
            <b-col>
                <b-table 
            show-empty 
            :fields="fieldsInv2" 
            :items="itemsInv2" 
            striped hover stacked
            empty-text="No hay datos para mostrar"
        >
            <template slot="seleccion" slot-scope="row">
            <b-form-checkbox v-model="seleccion[row.item.folio]" :key="row.item.folio" :value="row.item"/>
            </template>
            </b-table>
                </b-col>
            </b-row>
            </b-container>
    </b-tab>
    <b-tab title="Calendario de Pagos" :disabled="disabled[1]">
        <h1> por definir contenido</h1>
    </b-tab>
    <b-tab title="Documentos" :disabled="disabled[2]">
        <b-table 
            show-empty 
            :fields="fields" 
            :items="items" 
            :current-page="currentPage" 
            :per-page="perPage" 
            empty-text="No hay datos para mostrar"
        >
            <template slot="seleccion" slot-scope="row">
            <b-form-checkbox v-model="seleccion[row.item.folio]" :key="row.item.folio" :value="row.item"/>
            </template>
        </b-table>
        <b-pagination :total-rows="totalRows" :per-page="perPage" v-model="currentPage" class="my-0" />
    </b-tab>
  </b-tabs>
  </b-card>
      </b-modal>
  </div>
</template>

<script>

export default {
    name: 'Cesion',
    created(){
    },
    data () {
        return {
            itemsInv:[
                {
                    empresa:"Parmath",
                    monto_total:"$10.000.000",
                    monto_financiado:"$8.000.000",
                    nombre_proyecto:"Eco Escuela de Coaching OrgÃ¡nico"
                }
            ],
            fieldsInv:[
                {
                    label:"Empresa",
                    key:"empresa"
                },
                {
                    label:"Monto Total Solicitado",
                    key:"monto_total"
                },
                {
                    label:"Monto Financiado a la Fecha",
                    key:"monto_financiado"
                },
                {
                    label:"Nombre de Proyecto",
                    key:"nombre_proyecto"
                }
            ],
            itemsInv2:[],
            fieldsInv2:[],
            modalIndex:0,
            totalRows:0,
            perPage:10,
            currentPage:0,
            tabIndex:0,
            disabled:[false,false,false],
            filter:null,
            sortBy:[
                "En Curso",
                "Cerrada"
            ],
            modalInfo: { title: '', content: '' },
            items:[
                {
                    fecha:"14/07/2017",
                    proyecto:"Eco Escuela de Coaching Organico",
                    monto_solicitado:"$10.0000.000",
                    interes:"10%",
                    plazo:"12 meses",
                    monto_devolucion:"$11.000.000",
                    cuotas_pagadas:"4 de 12",
                    monto_isoluto:"$6.600.000",
                    operacion:"Activa"
                },
                {
                    fecha:"10/09/2017",
                    empresa:"Parmath",
                    monto_inversion:"$3.0000.000",
                    tir:"7,5%",
                    plazo:"6 meses",
                    monto_devolucion:"$3.202.500",
                    cuotas_pagadas:"12 de 12",
                    estado:"Cerrada"
                },
                {
                    fecha:"28/12/2017",
                    empresa:"Cultiva Segundo CrÃ©dito",
                    monto_inversion:"$1.5000.000",
                    tir:"8,2%",
                    plazo:"45 dÃ­as",
                    monto_devolucion:"$1.610.700",
                    cuotas_pagadas:"0 de 1",
                    estado:"En curso"
                }
            ],
            fields:[
                {
                    key:"fecha",
                    sortable:true
                },
                {
                    key:"proyecto",
                    sortable:true
                },
                {
                    key:"monto_solicitado",
                    sortable:true,
                },
                {
                    key:"interes",
                    label:"InterÃ©s"
                },
                {
                    key:"plazo",
                },
                {
                    key:"monto_devolucion",
                    label:"Monto a Devolver",
                },
                {
                    key:"cuotas_pagadas",
                },
                {
                    key:"monto_isoluto"
                },
                {
                    key:"operacion",
                    label:"OperaciÃ³n",
                    sortable:true
                },
                {
                    key:"deuda",
                    sortable:true
                },
            ],
            seleccion:{},
        }
    },
    methods:{
        info (item, index, evt) {
            this.modalInfo.title = `${item.empresa}`
            this.modalInfo.content = JSON.stringify(item, null, 2)
            this.$root.$emit('bv::show::modal', 'modalInfo', evt)
        },
        onSubmitCert(evt){
            console.log(this.formCert);
            //evt.preventDefault();
            /*
            setTimeout(()=>{
                this.tabIndex++
                this.disabled[0]=true;
            },100);
            */
            let data = new FormData();
            data.append("password",this.formCert.password);
            data.append("pfx",this.formCert.pfx,this.formCert.pfx.name);
            this.$http.post("http://redcapital.2050.cl:8081/APP/sii/login.js",data).then(res=>{
                console.log(res);
                if(res.body.length){
                    this.disabled[1]=false;
                    setTimeout(()=>{
                        this.tabIndex++;
                    },200);
                    this.empresa.inputs[0].options=res.body;
                }else{
                    //no llegaron empresas, tiene una sola, 
                    this.onSubmitEmp();
                }
            });
        },
        onSubmitEmp(evt){
            console.log(this.formEmp.empresa);
            let data = new FormData();
            this.disabled[2]=false;
            this.$http.post("http://redcapital.2050.cl:8081/APP/sii/getDTEList.js",this.formEmp).then(res=>{
                let {fields,items}=res.body;
                fields.unshift({label:"Seleccionar",key:"seleccion"});
                items.forEach(item=>{
                    this.seleccion[item.folio]=false;
                });
                this.totalRows=items.length;
                this.fields=fields;
                this.items=items;
                this.tabIndex++;
            }).catch(console.error);
        },
        onError(text){
            alert(text);
        },
        showSelect(){
            let seleccion = Object.keys(this.seleccion).filter(key=>this.seleccion[key]).map(id=>this.seleccion[id]);
            console.log(seleccion);
        },
        resetModal () {
            this.modalInfo.title = ''
            this.modalInfo.content = ''
        },
    },
    mounted(){
        console.log("ready");
    },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



